import React from 'react';
import Canvas from './Canvas';
import Inputs from './Inputs';
import { saveAs } from 'file-saver';
import { shareOnTwitter, presetToUrl } from '../utils/sharing';

const DEFAULT_PRESET = {
  name: 'default',
  seed: 1234,
  stars: 30000,
  spaceColor: '#020e26',
  starColor0: '#EEEDBE',
  starColor1: '#0388A6',
  starColor2: '#025059',
  starColor3: '#E9F2EB',
  starColor4: '#7CA67B',
  minStarSize: 0.8,
  maxStarSize: 2.3,
  density: 'easeInQuad',
  spread: 675,
  spreadShape: 'easeInCubic',
  sideShape: 'easeOutCubic',
  heightSpread: 0.1,
  height: 25,
  cameraX: 0.5,
  cameraY: 1.0,
  cameraZ: 2.0,
  mainGlow: 50,
  starGlow: 10,
  animated: false,
  speed: 20,
  coreColor: '#98C0E6',
  coreSize: 600,
  coreBlur: 30,
  starBlur: 0,
  type: 'spiral',
  arms: 2,
  armsCurve: 0.275,
};

const CANVAS_SIZE = 1500;

export default function App() {
  const canvasRef = React.useRef(null);

  React.useEffect(() => {
    window.mixpanel.track('app-loaded');
  });

  const [presets, setPresets] = React.useState({
    default: DEFAULT_PRESET,
    ...JSON.parse(localStorage.getItem('presets') || '{}'),
  });

  const [preset, setPresetValue] = React.useState(
    presets[localStorage.getItem('lastPreset')] || DEFAULT_PRESET
  );

  const onSave = () => {
    window.mixpanel.track('image-saved');
    canvasRef.current.toBlob(blob => {
      saveAs(blob, `galaxy-${preset.name}.png`);
    });
  };

  const onSavePreset = () => {
    window.mixpanel.track('preset-saved', preset);
    const newPresets = { ...presets, [preset.name]: preset };
    localStorage.setItem('presets', JSON.stringify(newPresets));
    setPresets(newPresets);
  };

  const onChangePreset = value => {
    const presetName = value.preset;
    window.mixpanel.track('preset-changed');
    setPresetValue(presets[presetName]);
    localStorage.setItem('lastPreset', presetName);
  };

  const onShare = () => {
    shareOnTwitter(presetToUrl(preset), 'I created a galaxy! ‚≠ê', [
      'icreatedagalaxy',
      'reversim2019',
    ]);
  };

  return (
    <div className="layout">
      <Canvas canvasRef={canvasRef} size={CANVAS_SIZE} preset={preset} />
      <Inputs
        presets={Object.keys(presets)}
        preset={preset}
        onSave={onSave}
        onSavePreset={onSavePreset}
        onChange={value => {
          window.mixpanel.track('input-changed', value);
          setPresetValue({ ...preset, ...value });
        }}
        onChangePreset={onChangePreset}
        onShare={onShare}
      />
    </div>
  );
}
