import React from 'react';
import { isSafari, isiOS } from '../utils/browser';
import { getGalaxyStars } from '../logic/galaxy';

const GLOBULAR_PADDING = 20;
const ANIMATION_SPEED_MULTIPLIER = 0.0001;
const GLOW_RATIO = 10;

export function draw(offscreenCanvas, presentedCtx, preset, time = 0) {
  const offscreenCanvasCtx = offscreenCanvas.getContext('2d');
  const canvasSize = offscreenCanvasCtx.canvas.width;
  const radius = canvasSize / 2;
  const transformedRadius =
    preset.type === 'globular' ? radius - GLOBULAR_PADDING : radius;

  const stars = getGalaxyStars(preset, transformedRadius, time);
  offscreenCanvasCtx.clearRect(0, 0, canvasSize, canvasSize);
  const imgData = offscreenCanvasCtx.getImageData(0, 0, canvasSize, canvasSize);
  stars.forEach(star =>
    drawStar(imgData, canvasSize, star.position, star.size, star.color)
  );
  offscreenCanvasCtx.putImageData(imgData, 0, 0);

  finalizeWithEffects(offscreenCanvas, presentedCtx, canvasSize, preset);
  addWatermark(presentedCtx, canvasSize);
}

function drawStar(imgData, canvasSize, { x, y }, size, { r, g, b }) {
  if (
    x > canvasSize / 2 ||
    x < -canvasSize / 2 ||
    y > canvasSize / 2 ||
    y < -canvasSize / 2
  ) {
    return;
  }
  const index =
    (Math.floor(x) +
      canvasSize / 2 +
      (Math.floor(y) + canvasSize / 2) * canvasSize) *
    4;
  const pixels = imgData.data;
  pixels[index + 0] = r;
  pixels[index + 1] = g;
  pixels[index + 2] = b;
  pixels[index + 3] = 255;
}

function finalizeWithEffects(offscreenCanvas, ctx, canvasSize, preset) {
  ctx.globalCompositeOperation = 'source-over';
  ctx.filter = 'none';
  ctx.fillStyle = preset.spaceColor;
  ctx.fillRect(0, 0, canvasSize, canvasSize);

  ctx.filter = `blur(${preset.mainGlow}px)`;
  ctx.drawImage(offscreenCanvas, 0, 0);

  ctx.globalCompositeOperation = 'screen';
  ctx.filter = `blur(${preset.mainGlow / GLOW_RATIO}px)`;
  ctx.drawImage(offscreenCanvas, 0, 0);

  ctx.globalCompositeOperation = 'lighten';
  ctx.filter = `blur(${preset.starGlow}px)`;
  ctx.drawImage(offscreenCanvas, 0, 0);

  if (!isSafari() && !isiOS()) {
    ctx.globalCompositeOperation = 'overlay';
    ctx.filter = `blur(${preset.coreBlur}px)`;
    const gradient = ctx.createRadialGradient(
      canvasSize / 2,
      canvasSize / 2,
      0,
      canvasSize / 2,
      canvasSize / 2,
      preset.coreSize / 2
    );
    gradient.addColorStop(0, 'white');
    gradient.addColorStop(0.5, preset.coreColor);
    gradient.addColorStop(1, 'transparent');
    ctx.fillStyle = gradient;
    ctx.fillRect(
      (canvasSize - preset.coreSize) / 2,
      (canvasSize - preset.coreSize) / 2,
      preset.coreSize,
      preset.coreSize
    );
  }

  ctx.globalCompositeOperation = 'screen';
  ctx.filter = `blur(${preset.starGlow}px)`;
  ctx.drawImage(offscreenCanvas, 0, 0);

  ctx.globalCompositeOperation = 'overlay';
  ctx.filter = 'none';
  ctx.drawImage(offscreenCanvas, 0, 0);

  ctx.globalCompositeOperation = 'none';
  ctx.filter = 'none';
  ctx.drawImage(offscreenCanvas, 0, 0);
}

function addWatermark(ctx, canvasSize) {
  ctx.globalCompositeOperation = 'source-over';
  ctx.filter = 'none';
  ctx.font = '24px Arial';
  ctx.fillStyle = 'rgba(255, 255, 255, 0.75)';
  ctx.fillText('creategalaxy.com', 8, canvasSize - 10);
}

//////////////////////////////////////////////////

let time = 0;

export default function Canvas({ size, preset, canvasRef }) {
  const offscreenCanvasRef = React.useRef(null);

  React.useLayoutEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    const offscreenCanvas = offscreenCanvasRef.current;
    let raf = null;

    const onFrame = () => {
      if (preset.animated) {
        time += (preset.speed || 0) * ANIMATION_SPEED_MULTIPLIER;
        draw(offscreenCanvas, ctx, preset, time);
        loopRaf();
      } else {
        draw(offscreenCanvas, ctx, preset, time);
      }
    };

    const loopRaf = () => {
      raf = window.requestAnimationFrame(onFrame);
    };

    const stopRaf = () => {
      window.cancelAnimationFrame(raf);
    };

    onFrame();

    return stopRaf;
  }, [preset, canvasRef]);

  return (
    <>
      <canvas
        className="offscreen-canvas"
        ref={offscreenCanvasRef}
        width={size}
        height={size}
      />
      <canvas className="canvas" ref={canvasRef} width={size} height={size} />
    </>
  );
}
