import React from 'react';
import { Easing } from '../utils/easing';

function input(type, preset, key, onChange, options = {}) {
  return (
    <div className="row">
      <div className="label">
        <label htmlFor={key}>{key}</label>
      </div>
      <div className="input">
        <input
          id={key}
          type={type}
          value={preset[key]}
          checked={type === 'checkbox' ? preset[key] : false}
          onChange={e =>
            onChange({
              [key]:
                type === 'checkbox'
                  ? e.target.checked
                  : type === 'number'
                  ? e.target.value === ''
                    ? 0
                    : parseFloat(e.target.value)
                  : e.target.value,
            })
          }
          onKeyUp={e => {
            if (e.keyCode === 13) {
              e.preventDefault();
              e.target.blur();
            }
          }}
          {...options}
        />
      </div>
      {type === 'range' && (
        <div className="value">
          <input type="text" readOnly value={preset[key]} />
        </div>
      )}
    </div>
  );
}

function select(value, key, options, onChange) {
  return (
    <div className="row">
      <div className="label">
        <label htmlFor={key}>{key}</label>
      </div>
      <div className="input">
        <select
          id={key}
          value={value}
          onChange={e => onChange({ [key]: e.target.value })}
        >
          {' '}
          {options.map(option => (
            <option key={option} value={option}>
              {option}
            </option>
          ))}
        </select>
      </div>
    </div>
  );
}

